import React from "react";


import { ActivityPreview } from "./ActivityPreview";
import { Loading } from "../Shared";
import { useInfiniteActivities } from './hooks/useInfiniteActivities';

type ActivityListProps = {
  filter: string;
}

export const ActivityList = ({ filter }: ActivityListProps) => {
  const {
    ref,
    activities,
    loading,
    hasNextPage
  } = useInfiniteActivities(filter);

  return (
    <div
      role="feed"
      aria-label="Feed of activities sorted by most recent"
      aria-busy={loading}
    >
      {activities.map((activity, index) => (
        <ActivityPreview
          key={index /*activity._id */}
          activity={activity}
          index={index}
          // totalCount={totalCount}
        />
      ))}
      {hasNextPage && (
        <span ref={ref} style={{ height: '1px' }}>
          {loading && <Loading />}
        </span>
      )}
    </div>
  );
};
